.overlay {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    transition: opacity .3s ease-in-out, visibility .3s ease-in-out;
       
    .drawer {
        display: flex;
        box-sizing: border-box;
        flex-direction: column;
        position: absolute;
        width: 385px;
        height: 100%;
        right: 0;
        background: #FFFFFF;
        box-shadow: -10px 4px 24px rgba(0, 0, 0, 0.1);
        padding: 30px;
        transform: translateX(100%);
        transition: transform 0.3s ease-in-out;  
  
        h2 {
            margin: 0;
            margin-bottom: 30px;
            font-size: 24px;

            display: flex;
            align-items: center;
            justify-content: space-between;

            img {
                opacity: .5;
                cursor: pointer;
                transition: opacity .15s ease-in-out;

                &:hover {
                    opacity: 1;
                }
            }
        }
        
        .items {
            display: flex;
            flex-direction: column;
            flex: 1;
            justify-content: space-between;
            overflow: hidden;
            
            .itemsInner {
            overflow: auto;
            margin-bottom: 30px;
                
                .cartItem {
                    display: flex;
                    align-items: center;
                    border: 1px solid #f3f3f3;
                    border-radius: 20px;
                    overflow: hidden;
                    padding: 20px;
                    margin-bottom: 20px;
                
                    .cartSneakers{
                        position: relative;
                        margin-right: 20px;
                        top: -6px;
                    }
                
                    .cartDescribe {
                        margin-right: 20px;
                        p {
                            font-size: 14px;
                            font-weight: 400;
                            margin: 0;
                            margin-bottom: 5px;
                        }
                    
                        b {
                            font-size: 14px;
                            font-weight: 700;
                        }
                    }
                
                    .removeBtn {
                        opacity: .5;
                        cursor: pointer;
                        transition: opacity .15s ease-in-out;
                    
                        &:hover {
                            opacity: 1;
                        }
                    }   
                }
            } 
        
            .drawerResult {
                padding: 0;
                margin: 0;
            
                li {
                    display: flex;
                    align-items: flex-end;
                    margin-bottom: 20px;
            
                    span {
                    font-size: 16px;
                    font-weight: 400;
                    }
                    
                    div {
                    flex: 1;
                    height: 1px;
                    border-bottom: 1px dashed #dfdfdf;
                    position: relative;
                    top: -4px;
                    margin: 0 8px;
                    }
            
                    b {
                    font-size: 16px;
                    }  
                }
            
                li:last-of-type {
                    margin-bottom: 0;
                }
            
                .greenButton {
                    position: relative;
                    margin-top: 24px;
            
                    width: 100%;
                    height: 55px;
            
                    background: #9DD558;
                    border-radius: 18px;
                    border: 0;
                    color: #fff;
                    font-size: 16px;
                    font-weight: 600;
                    padding-bottom: 3px;
                    cursor: pointer;
                    transition: background .2s ease-in-out;          
            
                    &:hover {
                        background: lighten(#9dd558, 7%);
                        
                        img {
                            transform: translateX(5px);
                        }
                    }
            
                    &:active {
                        background: darken(#9dd558, 7%);
                    }

                    &:disabled {
                        background: #bebebe;
                        cursor: default;
                        animation: button-loading 1s ease-in-out infinite;
                    }
                    
                    img {
                        position: absolute;
                        right: 30px;
                        top: 20px;
                        transition: transform .2s ease-in-out;
                    }
                }
            }
        } 
        // items            
    }
    // drawer
}

.overlayVisible {
    visibility: visible;
    opacity: 1;

    .drawer {
        transform: translateX(0);
    }
}
