.card {
  border: 1px solid #f8f8f8;
  margin-left: 15px;
  margin-right: 15px;
  margin-bottom: 30px;
  padding: 30px;
  width: 220px;
  height: 290px;
  border-radius: 40px;
  box-sizing: border-box;
  cursor: pointer;
  // background-color:#fefefe;
  transition: box-shadow 0.2s ease-in-out, transform 0.2s ease-in-out;

  &:hover {
    box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.05);
    transform: translateY(-5px);     
  }
  
  .favorite {
    position: absolute;
    cursor: pointer;
  }

  .sneakers {
    margin-left: 12px;
  }

  h5 {
    font-weight: 400;
    font-size: 14px;      
  }

  .cardBottom {
    display: flex;
    justify-content: space-between;
    align-items: center;

    div {
      display: flex;
      flex-direction: column;

      span {
        font-size: 11px;
        font-weight: 500;
        text-transform: uppercase;
        color: #BDBDBD;
        margin-bottom: 2px;
      }

      b {
        font-size: 14px;
        font-weight: 700;
      }
    }

    .plusButton {
      background: #fff;
      border:1px solid #F2F2F2;
      box-sizing: border-box;
      width: 32px;
      height: 32px;
      border-radius: 8px;
      cursor: pointer;

    }
  }
}
  
.greenBackButton {

  position: relative;
  margin-top: 24px;
  cursor: pointer;

  width: 250px;
  height: 55px;

  background: #9DD558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  transition: background .2s ease-in-out;
  padding-left: 32px;
  padding-bottom: 3px;

  &:hover {
    background: lighten(#9dd558, 7%);

    img {
      transform: translateX(5px);
    }
  }

  &:active {
    background: darken(#9dd558, 7%);
  }

  img {
    position: absolute;
    left: 30px;
    top: 20px;
    rotate: 180deg;
    transition: transform .2s ease-in-out;
  }
}