body {
  margin: 0;
  font-family: system-ui;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e7f6ff; 
}

a {
  text-decoration: none;
}

* {
  font-family: Inter;  
}

.wrapper {
  background: #FFFFFF;
  box-shadow: 0px 10px 20px rgba(0, 0, 0, 0.04);
  border-radius: 20px;
  max-width: 1080px;
  margin: 50px auto;  
}

// .overlay

.cartEmpty {
  display: flex;
  flex: 1;   
  flex-direction: column;
  justify-content: center;

  .cartEmptyInner {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: end;

    img {
      margin-bottom: 24px;
    }
    
    h2 {
      margin-top: 0;
      margin-bottom: 14px;
    }
    
    p {
      width: 285px;
      text-align: center;
      font-size: 16px;
      font-weight: 400;
      opacity: 40%;
      margin-top: 0;         
    }      
  }
}

.greenBackButton {
  position: relative;
  margin-top: 24px;
  cursor: pointer;

  width: 250px;
  height: 55px;

  background: #9DD558;
  border-radius: 18px;
  border: 0;
  color: #fff;
  font-size: 16px;
  font-weight: 600;
  transition: background .2s ease-in-out;
  padding-left: 32px;
  padding-bottom: 3px;

  &:hover {
    background: lighten(#9dd558, 7%);

    img {
      transform: translateX(5px);
    }
  }

  &:active {
    background: darken(#9dd558, 7%);
  }

  img {
    position: absolute;
    left: 30px;
    top: 20px;
    rotate: 180deg;
    transition: transform .2s ease-in-out;
  }
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 40px 55px;
  border-bottom: 1px solid #EAEAEA;

  .headerLeft {
    display: flex;
    align-items: center;
    
    img {
      margin-right: 15px;
    }

    h3,
    p {
      margin: 0;
    }

    h3 {
      text-transform: uppercase;
    }

    p {
      color: #9B9B9B;
    }

  }

  .headerRight {
    list-style-type: none;
    display: flex;

    a {
      margin-right: 30px;
      li {
        display: flex;
        align-items: center;
        cursor: pointer;
      }
    }

    a:last-of-type{
      margin-right: 0;
    }

    span {
      margin-left: 10px;
      font-size: 14px;
      font-weight: 600;
      color: #9B9B9B;
      transform: translateY(-4%);
    }
  }
}

.content {
  padding: 40px;
  
  .content-header {
    display: flex;
    align-items: center;
    margin: 0px 15px 40px 15px;
    justify-content: space-between;
    height: 51px;
    
    
    h1 {
      margin: 0;
      font-family: Inter;
      font-size: 32px;
      font-weight: 700;
      line-height: 39px;
      letter-spacing: 0em;
      text-align: left;
    }
  
    .search-block {
      display: flex;
      border: 1px solid #f3f3f3;
      border-radius: 10px;
      padding: 0 15px;
      align-items: center;

      input {
        border: 0;
        padding: 14px;
        font-size: 16px;
        font-weight: 400;
        color: #C4C4C4;
        outline: none;
      }

      .clearSearch {
        cursor: pointer;
        width: 22px;
        height: 22px;
        opacity: .6;
        transition: opacity .15s ease-in-out;
      }

      .clearSearch:hover {
        opacity: 1;
      }
    }
  }
}

.sneakers {
  display: flex;
  flex-wrap: wrap;
}

@keyframes button-loading {
  from {
    opacity: 1;    
  }

  50% {
    opacity: 0.75;
  }

  to {
    opacity: 1;
  }
}

