https://flickity.metafizzy.co
---------------------------------------------- */

.flickity-enabled {
position: relative;
}

.flickity-enabled:focus { outline: none; }

.flickity-viewport {
overflow: hidden;
position: relative;
max-height: 100%;
border-radius: 20px;
}

.flickity-slider {
position: absolute;
width: 100%;
height: 100%;
}

/* draggable */

.flickity-enabled.is-draggable {
-webkit-tap-highlight-color: transparent;
-webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
        user-select: none;
}

.flickity-enabled.is-draggable .flickity-viewport {
cursor: move;
cursor: -webkit-grab;
cursor: grab;
}

.flickity-enabled.is-draggable .flickity-viewport.is-pointer-down {
cursor: -webkit-grabbing;
cursor: grabbing;
}

/* ---- flickity-button ---- */

.flickity-button {
position: absolute;
background: white;
border: none;
color: #9B9B9B;
box-shadow: 0px 0px 14px 4px rgba(0,0,0,0.04);

}

.flickity-button:hover {
background: white;
cursor: pointer;
color: #606060;
}

.flickity-button:focus {
outline: none;
box-shadow: 0px 0px 14px 4px rgba(0,0,0,0.04); 
}

.flickity-button:active {
opacity: 1;
}

.flickity-button:disabled {
opacity: 1;
color: #dddddd;
cursor: auto;
/* prevent disabled button from capturing pointer up event. #716 */
pointer-events: none;
}

.flickity-button-icon {
fill: currentColor;
}

/* ---- previous/next buttons ---- */

.flickity-prev-next-button {
top: 50%;
width: 35px;
height: 35px;
border-radius: 50%;
/* vertically center */
transform: translateY(-50%);
}

.flickity-prev-next-button.previous { left: -17px; }
.flickity-prev-next-button.next { right: -17px; }
/* right to left */
.flickity-rtl .flickity-prev-next-button.previous {
left: auto;
right: 10px;
}
.flickity-rtl .flickity-prev-next-button.next {
right: auto;
left: 10px;
}

.flickity-prev-next-button .flickity-button-icon {
position: absolute;
left: 20%;
top: 20%;
width: 60%;
height: 60%;
}

/* ---- page dots ---- */

.flickity-page-dots {
position: absolute;
width: 100%;
bottom: -25px;
padding: 0;
margin: 0;
list-style: none;
text-align: center;
line-height: 1;
}

.flickity-rtl .flickity-page-dots { direction: rtl; }

.flickity-page-dots .dot {
display: inline-block;
width: 9px;
height: 9px;
margin: -2px 6px;
background: #9B9B9B;
border-radius: 50%;
opacity: 0.25;
cursor: pointer;
}

.flickity-page-dots .dot.is-selected {
opacity: 1;
}